import { Component } from 'react';
import { Container, Row } from 'react-bootstrap';
import SideNav from '../Sidenav'
import './style.css';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = { name: 'Rodrigo' };
      }
    render() {
        return (
            <Container fluid className="homebg p-lg-0">
                <SideNav/>
                <Container fluid="lg" className="homeCont"> 
                    <div className="homeCont_wrapper">
                        <h1>Hey there,<br /> I'm Rodrigo.<br /> I create engaging and<br /> pixel perfect websites.</h1> 
                        <h3>Front End Developer / Web Developer / Self-Taught Dev</h3>
                    </div>
                </Container>
            </Container>
            
        )
    }
}
export default Home;